.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 1vh;
}

.goBack{
    height: 4.5vh;
    cursor: pointer;
}

.settingsIcon{
    height: 5.5vh;
    cursor: pointer;
}


.headerSteps{
    display: flex;
    justify-content: center;
    padding: 2vh 0vh 2vh 0vh;
    width: 100%;
}

.headerSteps img:nth-child(2){
    padding: 0vh 2vh 0vh 2vh;
}

@media only screen and (max-width: 768px) and (max-height: 560px){
    .goBack{
        height: 22px;
    }

    .settingsIcon{
        height: 30px;
    }

    .headerSteps img:nth-child(2){
        padding: 0px 12px;
    }

    .headerSteps img{
        height: 8px;
    }

    .header{
        margin-top: 12px;
    }
}